<template>
  <div>
    <div>人数</div>
    <div>总访问人数：{{visitors.length}}</div>
    <div>外国人数： {{foriegn.length}}</div>
    <div>中国人数： {{china.length}}</div>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      visitors: [],
      foriegn: [],
      china: []
    }
  },
  created() {
    this.$axios.get('//gjs.so/painting2018/gjsadmindata').then(res => {
      this.visitors = res.data
      this.foriegn = res.data.filter(value => {
        return value.ip = 'EN'
      })
      this.china = res.data.filter(value => {
        return value.ip = 'CN'
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



